* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

#datetime-picker {
  margin-bottom: 20px;
  padding: 10px;
}

button {
  cursor: pointer;
  background-color: #ff69b4;
  border-radius: 8px;
  padding: 10px;
}

button:active {
  background-color: #ff1493;
}

button:hover {
  box-shadow: 0 12px 16px rgba(0, 0, 0, .24), 0 17px 50px #f904f5;
}

.timer {
  width: 80%;
  max-width: 600px;
  margin-top: 20px;
  display: flex;
}

.field {
  text-align: center;
  flex-direction: column;
  padding: .5rem;
  display: flex;
}

.value {
  margin-bottom: 5px;
  font-size: 2em;
}

.label {
  text-transform: uppercase;
  font-size: .8em;
}

